.App {
  text-align: center;
}

.App-body {
  background-color: #40CB8C;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  height: 100%;
}


table {
	border: 1px solid;
	width: 500px;
	margin-left: auto;
    margin-right: auto;
	text-align: center;
}

th, td {
  border: 1px solid;
  text-align: center;
  margin-left: auto;
   margin-right: auto;
}

tr:hover {
	background-color: #3A996E;
	text-align: center;
	margin-left: auto;
    margin-right: auto;
}

